.zeact_navbar {
    /* position: absolute; */
    min-height: 54px;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1000;
    /* box-shadow: 0px 2px 10px rgba(0,0,0,.3); */
    padding: 18px 0px;
}

.zeact_navbar_inner {
    position: absolute;

    left: 0;

    top: 0;

    width: 100%;

    height: 54px;

    display: -webkit-box;

    display: -webkit-flex;

    display: -ms-flexbox;

    display: flex;

    -webkit-box-align: center;

    -webkit-align-items: center;

    -ms-flex-align: center;

    align-items: center;

    -webkit-box-sizing: border-box;

    box-sizing: border-box;

    /* background-color: #fff; */
    /* justify-content: space-between; */
}

.zeact_navbar .zeact_navbar_left, .zeact_navbar .zeact_navbar_right, .zeact_navbar .zeact_navbar_title {

    position: relative;
    z-index: 1;

}

.zeact_navbar_title {
    font-size: 18px;
    font-weight: 600;
    /* padding: 0px 16px; */
}

.zeact_navbar_child {
    position: absolute;
    left: 0;

    top: 54px;
    background-color: #fff;
    width: 100%
}
.zeact_navbar_right {
    text-align: right
}