.zeact_button_sub_nav .zy_sub_nav a{
    display: inline-block;
    padding: 4px 0px;
    margin-left: 0px;
    font-size: 14px;
    color: #8d9297;
    font-weight: 600;
    position: relative;
    width: 50%;
    text-align: center;
}

.zeact_button_sub_nav .zy_sub_nav {
    padding: 0px;
    border: 1px solid #f2f2f2;
    border-radius: 4px;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
}

.zy_nav_index .zeact_navbar_title {
    padding: 0px
}

.zeact_button_sub_nav .zy_sub_nav a.active {
    color: #fff
}

.zeact_button_sub_nav .zy_sub_nav a.active:after {
    width: 100%;
    margin: 0px;
    left: 0px;
    height: 100%;
    border-radius: 0px;
    z-index: -1;
}

.zeact_button_sub_nav .zy_sub_nav a:first-of-type.active:after {
    background: -webkit-linear-gradient(0deg, #799dd2 ,#5d7bc3) !important; /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(0deg, #799dd2 ,#5d7bc3) !important; /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(0deg, #799dd2 ,#5d7bc3) !important; /* Firefox 3.6 - 15 */
    background: linear-gradient(90deg, #799dd2 ,#5d7bc3) !important; /* 标准的语法 */
    color: #fff  !important;
}

.zeact_button_sub_nav .zy_sub_nav a:last-of-type.active:after {
    background: -webkit-linear-gradient(0deg, #7f7aa7, #6a6292) !important; /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(0deg, #7f7aa7, #6a6292) !important; /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(0deg, #7f7aa7, #6a6292) !important; /* Firefox 3.6 - 15 */
    background: linear-gradient(90deg, #7f7aa7, #6a6292) !important; /* 标准的语法 */
    color: #fff  !important;
}

