

.prize_pool {
    text-align: center;
    color: #dfe5ef;
    padding: 24px 0px 0px;
}

.prize_pool_title {
    padding: 18px 0px;
    font-size: 28px;
}
.prize_pool_number span{
    /* font-size: 28px;
    margin-left: 14px; */
    padding: 4px 8px;
    margin: 0px 2px;
    background-color: #607ec4;
    border-radius: 8px;
    text-shadow: 0px 0px 4px #e7eaef;
}

.prize_pool_number {
    margin: 8px 0px;
    font-size: 50px;
    letter-spacing: 3px
}

.prize_pool_sub {
    position: relative;
    padding: 18px 0px;
    width: 40%;
    margin:  14px auto 0px;
}

.prize_pool_sub:before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    height: 1px;
    width: 100%;
    background-color: #333639;
    z-index: 0;
}

.prize_pool_sub p {
    background-color: #292c2f;
    width: 26%;
    margin: auto;
    padding: 10px 0px;
    font-size: 18px;
    z-index: 0;
    position: relative;
}